import React, { useEffect, useRef, useState } from "react";
import { GLTFLoader } from "three/examples/jsm/loaders/GLTFLoader";
import { Text, Sphere, CatmullRomLine, Billboard } from "@react-three/drei";
import * as THREE from "three";
import { useLoader } from "@react-three/fiber";
import useZoomLevel from "./useZoomLevel";

export default function Model({ toggleState, liftData }) {
  const gltf = useLoader(GLTFLoader, "./LP2.glb");

  const boundingBox = new THREE.Box3().setFromObject(gltf.scene);
  const zoomLevel = useZoomLevel(boundingBox);

  const hasAncestorWithName = (object, name) => {
    while (object) {
      if (object.name === name) return true;
      object = object.parent;
    }
    return false;
  };

  const defaultTopologyMaterial = useRef(null);
  const whiteMaterial = useRef(
    new THREE.MeshStandardMaterial({
      color: "white",
      side: THREE.DoubleSide,
      roughness: 0,
    })
  );

  const initializeMeshes = () => {
    const emptyObjects = [];
    const liftObjects = [];

    gltf.scene.traverse((child) => {
      if (child.isMesh) {
        if (hasAncestorWithName(child, "Topography")) {
          child.castShadow = true;
          child.receiveShadow = true;
          if (defaultTopologyMaterial.current === null) {
            defaultTopologyMaterial.current = child.material.clone(); // Clone the material only once to prevent overwriting!
          }
        } else if (hasAncestorWithName(child, "Buildings")) {
          child.castShadow = true;
          child.receiveShadow = true;
        }
      } else if (
        (child.type === "Group" || child.type === "Object3D") &&
        child.name.startsWith("Town_")
      ) {
        emptyObjects.push(child);
      }
      if (child.name.startsWith("Lifts_")) {
        liftObjects.push(child);
      }
    });

    return { emptyObjects, liftObjects };
  };

  const { emptyObjects, liftObjects } = initializeMeshes();

  const towns = emptyObjects.map((obj) => {
    const name = obj.name.split("_").slice(1).join(" "); // Split the name and join with a space
    return {
      name: name,
      position: obj.position,
    };
  });

  console.log("Mapping liftObjects", liftObjects.length);

  const lifts = liftObjects.map((obj) => {
    const name = obj.name.split("_").slice(1).join(" ");
    const lift = liftData().skiLifts.find((lift) => lift.name === name);
    const status = lift ? lift.status : "unknown";

    return {
      name: name,
      position: obj.position,
      status: status,
    };
  });

  useEffect(() => {
    gltf.scene.traverse((child) => {
      if (child.isMesh && hasAncestorWithName(child, "Topography")) {
        child.material = toggleState.satellite
          ? whiteMaterial.current
          : defaultTopologyMaterial.current.clone();
      }
    });
  }, [toggleState.satellite, gltf.scene]);

  const [curves, setCurves] = useState([]);

  useEffect(() => {
    fetch("/curve_data.json")
      .then((response) => response.json())
      .then((data) => {
        console.log(data); // logs the fetched data to the console
        setCurves(data);
      })
      .catch((error) => console.error(error));
  }, []);

  const getColor = (difficulty, state) => {
    if (state) {
      switch (difficulty) {
        case "novice":
          return "#01AB28";
        case "easy":
          return "#0012DA";
        case "intermediate":
          return "#FA1252";
        case "advanced":
          return "black";
        default:
          return "0x555555";
      }
    } else {
      switch (difficulty) {
        case "novice":
          return "#01FF10";
        case "easy":
          return "#0066FF";
        case "intermediate":
          return "#F7193E";
        case "advanced":
          return "black";
        default:
          return "0x555555";
      }
    }
  };

  const [hoveredCurve, setHoveredCurve] = useState(null);

  return (
    <>
      <primitive object={gltf.scene} />
      {toggleState.resortLabels &&
        towns.map((obj) => (
          <Text
            key={obj.name}
            position={[obj.position.x, obj.position.y + 1.2, obj.position.z]}
            rotation={[Math.PI / 2, Math.PI, 0]}
            material={
              new THREE.MeshBasicMaterial({ color: 0x000000, fog: null })
            }
            fontSize={0.4}
            fillOpacity={zoomLevel}
            font="./Rubik-Medium.ttf"
          >
            {obj.name.toUpperCase()}
          </Text>
        ))}
      {curves.map((curve, index) => {
        if (curve.name.startsWith("Lifts_")) {
          const name = curve.name.split("_").slice(1).join(" ");
          const lift = liftData().skiLifts.find((lift) => lift.name === name);
          const status = lift ? lift.status : "unknown";

          let position;
          if (curve.points.length === 2) {
            position = [
              (curve.points[0][0] + curve.points[1][0]) / 2,
              (curve.points[0][1] + curve.points[1][1]) / 2 + 0.15,
              (curve.points[0][2] + curve.points[1][2]) / 2,
            ];
          } else {
            position = [
              curve.points[Math.floor(curve.points.length / 2)][0],
              curve.points[Math.floor(curve.points.length / 2)][1] + 0.15,
              curve.points[Math.floor(curve.points.length / 2)][2],
            ];
          }

          return (
            <>
              {toggleState.liftLabels && (
                <>
                  <CatmullRomLine
                    position={[0, 0.05, 0]}
                    points={curve.points}
                    color={"#111111"}
                    segments={100}
                    lineWidth={1.5}
                    curveType="centripetal"
                    onPointerOver={() => setHoveredCurve(curve.name)}
                    onPointerOut={() => setHoveredCurve(null)}
                    key={index}
                    fog
                    depthTest
                    depthWrite
                    visible={zoomLevel < 0.5}
                  />
                  <Billboard position={position}>
                    <Text
                      rotation={[0, 0, 0]}
                      color={"white"}
                      fontSize={0.06}
                      outlineWidth={0.004}
                      outlineColor={status === "open" ? "black" : "red"}
                      font="./Rubik-Medium.ttf"
                      toneMapped={false}
                      visible={zoomLevel < 0.5}
                    >
                      {curve.properties.name}
                    </Text>
                  </Billboard>
                </>
              )}
            </>
          );
        } else {
          return (
            <>
              {toggleState.runs && (
                <>
                  <CatmullRomLine
                    position={[0, -0.01, 0]}
                    points={curve.points}
                    color={getColor(
                      curve.properties["piste:difficulty"],
                      toggleState.satellite
                    )}
                    segments={175}
                    lineWidth={hoveredCurve === curve.name ? 5 : 0.5}
                    curveType="centripetal"
                    onPointerOver={() => setHoveredCurve(curve.name)}
                    onPointerOut={() => setHoveredCurve(null)}
                    key={index}
                    fog
                    depthTest
                    depthWrite
                  />
                  <Billboard
                    position={[
                      curve.points[Math.floor(curve.points.length / 2)][0],
                      curve.points[Math.floor(curve.points.length / 2)][1] +
                        0.15,
                      curve.points[Math.floor(curve.points.length / 2)][2],
                    ]}
                  >
                    <Text
                      rotation={[0, 0, 0]}
                      color={"black"}
                      fontSize={0.05}
                      outlineWidth={0.004}
                      outlineColor={"white"}
                      font="./Rubik-Regular.ttf"
                      toneMapped={false}
                      visible={hoveredCurve === curve.name}
                    >
                      {curve.properties.name}
                    </Text>
                  </Billboard>
                </>
              )}
            </>
          );
        }
      })}
    </>
  );
}
